
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'LoginPanel',
  props: {
    backgroundColor: {
      type: String,
      default: '#20b2aa'
    },
    title: String,
    titleColor: {
      type: String,
      default: '#ffffff'
    },
    subTitle: String,
    subTitleColor: {
      type: String,
      default: '#ffffff'
    },
    btnText: String,
    btnTextColor: {
      type: String,
      default: '#ffffff'
    },
    btnBgColor: {
      type: String,
      default: '#20b2aa'
    },
    btnClick: Function,
    resetForm: Function
  },
  setup(props) {
    let cursor = 'auto'
    if (props.resetForm) {
      cursor = 'pointer'
    }
    return {
      cursor
    }
  }
})
