<template>
  <div class="login" id="login">
    <canvas-nest el="#login" />
    <div class="login-content">
      <login-panel
        v-bind="login"
        class="signin-panel"
        :class="{ 'login-panel-inactive': show == 'registe' }"
      >
        <login-form ref="loginForm" />
      </login-panel>
      <login-panel
        v-bind="show == 'login' ? showRegiste : showLogin"
        class="switch-panel"
        :class="{ 'show-login-panel': show == 'registe' }"
      />
      <login-panel
        v-bind="registe"
        class="registe-panel"
        :class="{ 'registe-panel-active': show == 'registe' }"
        :resetForm="resetRegisteForm"
      >
        <registe-form ref="registeForm" />
      </login-panel>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'
import LoginForm from './LoginForm.vue'
import LoginPanel from './LoginPanel.vue'
import RegisteForm from './RegisteForm.vue'
import CanvasNest from '@/components/canvas-nest/src/CanvasNest.vue'
import { login as postLogin, registe as registeNewUser } from '@/service/login'
import {
  loginPanel,
  registePanel,
  switchRegistePanel,
  switchLoginPanel,
  panelColor
} from '../config'
import { UserInfo } from '../types'

export default defineComponent({
  components: { LoginPanel, LoginForm, RegisteForm, CanvasNest },
  name: 'Login',
  setup() {
    // 获取登录面板组件
    const loginForm = ref(null)
    // 获取注册面板组件
    const registeForm = ref(null)
    // 提交登录信息
    const commitUserInfo = () => {
      ;(loginForm.value as any)
        .checkForm()
        .then((res: { state: boolean; data: UserInfo }) => {
          if (res.state) {
            postLogin(res.data)
          }
        })
    }
    // 提交注册信息
    const commitRegisteInfo = () => {
      ;(registeForm.value as any)
        .checkForm()
        .then((res: { state: boolean; data: any }) => {
          if (res.state) {
            registeNewUser(res.data)
          }
        })
    }
    // 绑定到登录面板
    const login = { btnClick: commitUserInfo, ...loginPanel, ...panelColor }
    // 绑定到注册面板
    const registe = {
      btnClick: commitRegisteInfo,
      ...registePanel,
      ...panelColor
    }
    // 切换面板的状态 show==login 为登录页, show==registe 为注册页
    const show = ref('login')
    // 改变切换面板的状态
    const changeShow = () => {
      show.value == 'login' ? (show.value = 'registe') : (show.value = 'login')
      return true
    }
    // 切换到注册面板时，切换面板的信息
    const showRegiste = { btnClick: changeShow, ...switchRegistePanel }
    // 切换到登录面板时，切换面板的信息
    const showLogin = { btnClick: changeShow, ...switchLoginPanel }
    // 重置注册表单
    const resetRegisteForm = () => {
      ;(registeForm.value as any).elForm.resetFields()
    }
    return {
      show,
      changeShow,
      login,
      registe,
      showRegiste,
      showLogin,
      loginForm,
      registeForm,
      resetRegisteForm
    }
  }
})
</script>

<style scoped lang="less">
.login {
  min-width: 1200px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  .login-content {
    width: 1060px;
    height: 550px;
    border-radius: 10px;
    position: relative;
    overflow: hidden;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    .login-panel {
      position: absolute;
      top: 0;
      transition: all 0.6s ease-in-out;
    }
    .signin-panel {
      left: 0;
    }
    .registe-panel {
      left: 0;
      opacity: 0;
      pointer-events: none;
    }
    .switch-panel {
      left: 50%;
      z-index: 5;
    }
    .login-panel-inactive {
      transform: translateY(100%);
    }
    .show-login-panel {
      transform: translateX(-100%);
    }
    .registe-panel-active {
      transform: translateX(530px);
      opacity: 1;
      pointer-events: auto;
    }
  }
}
</style>
