<template>
  <div class="login-panel" :style="{ backgroundColor: backgroundColor }">
    <h1 class="title" :style="{ color: titleColor }">{{ title }}</h1>
    <p
      class="sub-title"
      :style="{ color: subTitleColor, cursor: cursor }"
      @click="resetForm"
    >
      {{ subTitle }}
    </p>
    <slot></slot>
    <el-button
      class="btn"
      @click="btnClick"
      :style="{ color: btnTextColor, backgroundColor: btnBgColor }"
    >
      {{ btnText }}
    </el-button>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue'

export default defineComponent({
  name: 'LoginPanel',
  props: {
    backgroundColor: {
      type: String,
      default: '#20b2aa'
    },
    title: String,
    titleColor: {
      type: String,
      default: '#ffffff'
    },
    subTitle: String,
    subTitleColor: {
      type: String,
      default: '#ffffff'
    },
    btnText: String,
    btnTextColor: {
      type: String,
      default: '#ffffff'
    },
    btnBgColor: {
      type: String,
      default: '#20b2aa'
    },
    btnClick: Function,
    resetForm: Function
  },
  setup(props) {
    let cursor = 'auto'
    if (props.resetForm) {
      cursor = 'pointer'
    }
    return {
      cursor
    }
  }
})
</script>

