<template>
  <div class="registe-form">
    <el-form :rules="registeFormRules" :model="info" ref="elForm">
      <div
        class="registe-item"
        v-for="(item, index) in registePanelInfo"
        :key="index"
      >
        <el-form-item v-bind="item.formItem">
          <el-input
            class="registe-input"
            v-model="info[item.formItem.prop]"
            size="small"
            v-bind="item.inputItem"
            spellcheck="false"
          />
        </el-form-item>
        <el-button
          class="btn-verify-code"
          v-if="item.formItem.prop == 'email'"
          @click="getVerify"
          >获取验证码</el-button
        >
        <el-select
          class="select-box"
          v-if="item.formItem.prop == 'name'"
          v-model="info.department"
          placeholder="选择学院"
        >
          <el-option
            v-for="option in selectionInfo"
            :value="option.value"
            :label="option.label"
            :key="option.value"
          />
        </el-select>
      </div>
      <div class="registe-item">
        <transition name="opacity-switch" mode="out-in">
          <p v-if="!info.admin" class="registe-admin" @click="changeAdmin">
            注册管理员?
          </p>
          <div v-else class="registe-item">
            <el-input
              class="registe-input"
              v-model="info.adminVerify"
              size="small"
              placeholder="管理员注册码"
              clearable
            />
            <el-button class="btn-verify-code" @click="changeAdmin"
              >取消</el-button
            >
          </div>
        </transition>
      </div>
    </el-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'
import { registePanelInfo, selectionInfo, registeFormRules } from '../config'
import { getEmailCode } from '@/service/login'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'RegisteForm',
  setup() {
    // 获取表单组件
    const elForm = ref(null)
    const info = ref({
      username: '',
      name: '',
      email: '',
      emailVerify: '',
      password: '',
      passwordVerify: '',
      department: '',
      admin: false,
      adminVerify: ''
    })
    // 切换admin，记录是否注册管理员
    const changeAdmin = () => {
      info.value.admin = !info.value.admin
    }
    // 获取邮箱验证码
    const getVerify = () => {
      getEmailCode(info.value.username, info.value.email)
    }
    // 过滤info，只要后端接口需要的数据
    const filterInfo = () => {
      const {
        username,
        name,
        email,
        emailVerify,
        password,
        department,
        admin,
        adminVerify
      } = info.value
      return {
        username,
        name,
        email,
        emailVerify,
        password,
        department,
        admin,
        adminVerify
      }
    }
    // 检查表单是否基本填写完成
    const checkForm = () => {
      return new Promise((resolve) => {
        ;(elForm.value as any).validate((complete: boolean) => {
          if (complete) {
            // 没有选择所属学院
            if (info.value.department == '') {
              ElMessage({
                message: '请选择所在的学院',
                type: 'warning'
              })
              resolve({ state: false, data: filterInfo() })
            }
            // 注册管理员时，没有填写邀请码
            if (info.value.admin && info.value.adminVerify == '') {
              ElMessage({
                message: '请输入管理员邀请码',
                type: 'warning'
              })
              resolve({ state: false, data: filterInfo() })
            }
            resolve({ state: true, data: filterInfo() })
          }
        })
      })
    }
    return {
      info,
      elForm,
      registeFormRules,
      registePanelInfo,
      changeAdmin,
      selectionInfo,
      getVerify,
      checkForm
    }
  }
})
</script>

