      display: flex;
      align-items: center;
      justify-content: center;
      &:last-child {
        margin-bottom: 0;
      }
      .el-form-item {
        margin-bottom: 0;
        flex: 1;
      }
      .btn-verify-code {
        min-width: 80px;
        margin-left: 12px;
        padding: 0 8px;
        font-size: 12px;
        color: #fff;
        background-color: var(--login-switch-bg);
        border: 1px solid #fff;
      }
      .select-box {
        width: 120px;
