
import { defineComponent, ref } from 'vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'LoginForm',
  setup() {
    const userInfo = ref({ username: '', password: '' })
    const forgetPassword = () => {
      ElMessage({
        message: '忘记了我也没有办法~',
        type: 'error'
      })
    }
    // 检查表单是否基本填写
    const checkForm = () => {
      return new Promise((resolve) => {
        if (userInfo.value.username == '' || userInfo.value.password == '') {
          ElMessage({
            message: '用户名或密码不能为空',
            type: 'warning'
          })
          resolve({ state: false, data: userInfo.value })
        } else {
          resolve({ state: true, data: userInfo.value })
        }
      })
    }
    return {
      userInfo,
      forgetPassword,
      checkForm
    }
  }
})
