<template>
  <div class="login-form">
    <el-form>
      <el-form-item prop="name">
        <el-input
          class="login-input"
          v-model="userInfo.username"
          placeholder="请输入用户名(学号)"
          maxlength="24"
          clearable
        />
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          class="login-input"
          v-model="userInfo.password"
          placeholder="请输入密码"
          maxlength="16"
          clearable
          show-password
          @keyup.enter="jumpToMain(userInfo)"
        />
      </el-form-item>
      <el-form-item>
        <p class="forget-password" @click="forgetPassword">忘记密码?</p>
      </el-form-item>
    </el-form>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref } from 'vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'LoginForm',
  setup() {
    const userInfo = ref({ username: '', password: '' })
    const forgetPassword = () => {
      ElMessage({
        message: '忘记了我也没有办法~',
        type: 'error'
      })
    }
    // 检查表单是否基本填写
    const checkForm = () => {
      return new Promise((resolve) => {
        if (userInfo.value.username == '' || userInfo.value.password == '') {
          ElMessage({
            message: '用户名或密码不能为空',
            type: 'warning'
          })
          resolve({ state: false, data: userInfo.value })
        } else {
          resolve({ state: true, data: userInfo.value })
        }
      })
    }
    return {
      userInfo,
      forgetPassword,
      checkForm
    }
  }
})
</script>

<style lang="less" scoped>
.login-form {
  width: 100%;
  .el-form {
    width: 350px;
    margin: 0 auto;
    background: var(--bg-color-5);
    .forget-password {
      width: 30%;
      margin: 0 auto;
      color: #333333;
      font-size: 14px;
      text-align: center;
      cursor: pointer;
    }
  }
}
</style>
