import { dfRequest } from '../request'
import { ElMessage } from 'element-plus'
import qs from 'qs'
import { RegisteInfo } from '@/views/login/types'

export function registe(registeInfo: RegisteInfo) {
  return new Promise(() => {
    dfRequest
      .post({
        url: '/api/register/',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify(registeInfo)
      })
      .then((res) => {
        if (res.status === 'success') {
          ElMessage({
            message: '注册成功',
            type: 'success'
          })
        } else {
          ElMessage({
            message: '注册失败',
            type: 'error'
          })
        }
      })
      .catch((err) => {
        console.log(err)
        ElMessage({
          message: '注册失败',
          type: 'error'
        })
      })
  })
}
