import router from '@/router'
import { ElMessage } from 'element-plus'
import { UserInfo } from '@/views/login'
import { dfRequest } from '@/service/request'
import qs from 'qs'

export function login(userInfo: UserInfo): void {
  dfRequest
    .post({
      url: '/api/login/',
      headers: {
        'Content-type': 'application/x-www-form-urlencoded'
      },
      data: qs.stringify(userInfo)
    })
    .then((res) => {
      if (res.status === 'success') {
        ElMessage({
          message: '登录成功',
          type: 'success'
        })
        window.sessionStorage.setItem('login', 'true')
        window.sessionStorage.setItem('admin', res.data.admin)
        router.push({ path: '/user-info' })
      } else {
        ElMessage({
          message: '登录失败',
          type: 'error'
        })
      }
    })
    .catch((err) => {
      console.log(err)
      ElMessage({
        message: '登录失败',
        type: 'error'
      })
    })
}
