import { dfRequest } from '../request'
import { ElMessage } from 'element-plus'
import qs from 'qs'

export function getEmailCode(username: string, email: string) {
  return new Promise(() => {
    dfRequest
      .post({
        url: '/api/getemailcode/',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify({ username, email })
      })
      .then((res) => {
        if (res.status === 'success') {
          ElMessage({
            message: '验证码已发送到您的邮箱',
            type: 'success'
          })
        } else {
          ElMessage({
            message: '发送验证码失败',
            type: 'error'
          })
        }
      })
      .catch((err) => {
        console.log(err)
        ElMessage({
          message: '发送验证码失败',
          type: 'error'
        })
      })
  })
}
