import { dfRequest } from '../request'
import qs from 'qs'

export function checkUserName(username: string) {
  return new Promise((resolve) => {
    dfRequest
      .post({
        url: '/api/checkusername/',
        headers: {
          'Content-type': 'application/x-www-form-urlencoded'
        },
        data: qs.stringify({ username })
      })
      .then((res) => {
        resolve(res.data)
      })
      .catch((err) => {
        console.log(err)
      })
  })
}
