
import { defineComponent, ref } from 'vue'
import { registePanelInfo, selectionInfo, registeFormRules } from '../config'
import { getEmailCode } from '@/service/login'
import { ElMessage } from 'element-plus'

export default defineComponent({
  name: 'RegisteForm',
  setup() {
    // 获取表单组件
    const elForm = ref(null)
    const info = ref({
      username: '',
      name: '',
      email: '',
      emailVerify: '',
      password: '',
      passwordVerify: '',
      department: '',
      admin: false,
      adminVerify: ''
    })
    // 切换admin，记录是否注册管理员
    const changeAdmin = () => {
      info.value.admin = !info.value.admin
    }
    // 获取邮箱验证码
    const getVerify = () => {
      getEmailCode(info.value.username, info.value.email)
    }
    // 过滤info，只要后端接口需要的数据
    const filterInfo = () => {
      const {
        username,
        name,
        email,
        emailVerify,
        password,
        department,
        admin,
        adminVerify
      } = info.value
      return {
        username,
        name,
        email,
        emailVerify,
        password,
        department,
        admin,
        adminVerify
      }
    }
    // 检查表单是否基本填写完成
    const checkForm = () => {
      return new Promise((resolve) => {
        ;(elForm.value as any).validate((complete: boolean) => {
          if (complete) {
            // 没有选择所属学院
            if (info.value.department == '') {
              ElMessage({
                message: '请选择所在的学院',
                type: 'warning'
              })
              resolve({ state: false, data: filterInfo() })
            }
            // 注册管理员时，没有填写邀请码
            if (info.value.admin && info.value.adminVerify == '') {
              ElMessage({
                message: '请输入管理员邀请码',
                type: 'warning'
              })
              resolve({ state: false, data: filterInfo() })
            }
            resolve({ state: true, data: filterInfo() })
          }
        })
      })
    }
    return {
      info,
      elForm,
      registeFormRules,
      registePanelInfo,
      changeAdmin,
      selectionInfo,
      getVerify,
      checkForm
    }
  }
})
