// 注册面板的 表单校验规则
const customRule = {
  required: true,
  message: '不可为空',
  trigger: 'blur'
}
// 记录两次输入的密码，进行校对
let _password_1 = ''
let _password_2 = ''
export const registeFormRules = {
  username: [
    customRule,
    { min: 10, message: '学号位数为10', trigger: 'blur' },
    {
      asyncValidator(rule: any, value: string, callback: any) {
        checkUserName(value).then((res: any) => {
          if (!res.usable) {
            callback(new Error('用户名不可用'))
          } else {
            callback()
          }
        })
      },
      trigger: 'blur'
    }
  ],
  name: [customRule],
  email: [
    customRule,
    { type: 'email', message: '邮箱格式错误', trigger: 'blur' }
  ],
  emailVerify: [customRule],
  password: [
    customRule,
    {
      validator: (rule: any, value: string, callback: any) => {
        _password_1 = value
        if (_password_2 != '' && _password_1 != _password_2) {
          callback(new Error('两次输入的密码不一致'))
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ],
  passwordVerify: [
    customRule,
    {
      validator: (rule: any, value: string, callback: any) => {
        _password_2 = value
        if (_password_1 != _password_2) {
          callback(new Error('两次输入的密码不一致'))
        } else {
          callback()
        }
      },
      trigger: 'change'
    }
  ]
}
